var app = getApp().globalData
export default {
	data() {
		return {
			bluekey:'',
			n:0,
			value:'',
			hot_search:[],
			dataList:['全部','人脉','资讯','路顶记','小申展','项目/机会'],
			globalList:[],
			personList:[],
			informationList:[],
			luList:[],
			xiaoList:[],
			huiList:[],
			pagePerson:1,
			loadingPerson:false,
			allowPerson:false,
			pageInformation:1,
			loadingInformation:false,
			allowInformation:false,
			
			pageLu:1,
			loadingLu:false,
			allowLu:false,
			
			pageXiao:1,
			loadingXiao:false,
			allowXiao:false,
			
			pageHui:1,
			loadingHui:false,
			allowHui:false,
			showTitle1:false,
			showTitle2:false,
			showTitle3:false,
			showTitle4:false,
			showTitle5:false,
			showTitle6:false
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: '德申汇惠全球',
		  imageUrl:app.shareImg,
		  path: '/pagesMy/Search/Search'
		}
	},
	// 触底加载
	onReachBottom(){
		if(this.allowPerson&&this.n===1){	
			this.allowPerson = false				
			this.loadingPerson = true
			this.pagePerson++
			this.searchPerson()
			// setTimeout(() => {
				
			// 	this.pagePerson++
			// 	this.searchPerson()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
		
		if(this.allowInformation&&this.n===2){
			this.allowInformation = false				
			this.loadingInformation = true
			this.pageInformation++
			this.searchInformation()
			// setTimeout(() => {
			// 	this.pageInformation++
			// 	this.searchInformation()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
		
		if(this.allowLu&&this.n===3){
			this.allowLu = false				
			this.loadingLu = true
			this.pageLu++
			this.searchLu()
			// setTimeout(() => {
			// 	this.pageLu++
			// 	this.searchLu()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
		
		if(this.allowXiao&&this.n===4){
			this.allowXiao = false				
			this.loadingXiao = true
			this.pageXiao++
			this.searchXiao()
			// setTimeout(() => {
			// 	this.pageXiao++
			// 	this.searchXiao()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
		
		if(this.allowHui&&this.n===5){
			this.allowHui = false				
			this.loadingHui = true
			this.pageHui++
			this.searchHui()
			// setTimeout(() => {
			// 	this.pageHui++
			// 	this.searchHui()
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	onLoad() {
		this.everybody()
	},
	methods: {
		
		// 资讯详情
		imdetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InfoDetail/InfoDetail?id='+id
			})
		},
		// 路顶记详情
		LuDingJiDetails(id){
			uni.navigateTo({
				url:'../../pagesMy/LuDingJiDetails/LuDingJiDetails?id='+id
			})
		},
		// 小申展详情
		SmallStretchPvw(id){
			uni.navigateTo({
				url:'../../pagesMy/SmallStretchPvw/SmallStretchPvw?id='+id
			})
		},
		// 项目详情
		InformationDetail(id){
			uni.navigateTo({
				url:'../../pagesMy/InformationDetail/InformationDetail?id='+id
			})
		},
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		hot(key){
			this.value= key
			uni.showLoading({
			    title: '加载中'
			});
			setTimeout(()=>{
				this.searchIndex()
			}, 300);
			
		},
		
		tab(ind){
			this.n = ind
			if(this.n==0){
				uni.showLoading({
				    title: '加载中'
				});
				setTimeout(()=>{
					this.searchIndex()
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
				}, 300);
			}
			
			if(this.n==1){
				this.pagePerson = 1
				uni.showLoading({
				    title: '加载中'
				});
				setTimeout(()=>{
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
					this.searchPerson()
				}, 300);
			}
			
			if(this.n==2){
				this.pageInformation = 1
				uni.showLoading({
				    title: '加载中'
				});
				setTimeout(()=>{
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
					this.searchInformation()
				}, 300);
			}
							
			if(this.n==3){
				this.pageLu = 1
				uni.showLoading({
					title: '加载中'
				});
				setTimeout(()=>{
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
					this.searchLu()
				}, 300);
			}
			
			if(this.n==4){
				this.pageXiao = 1
				uni.showLoading({
					title: '加载中'
				});
				setTimeout(()=>{
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
					this.searchXiao()
				}, 300);
			}
							
			if(this.n==5){
				this.pageHui = 1
				uni.showLoading({
					title: '加载中'
				});
				setTimeout(()=>{
					uni.pageScrollTo({
						scrollTop:0,
						duration:0
					})
					this.searchHui()
				}, 300);
			}						
							
		},
		
		// 会友汇
		searchHui(){
			this.$api.op_index_search({
				search:this.value,
				page:this.pageHui
			}).then(res=>{
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.pageHui===1){
						uni.hideLoading();
						this.huiList = res.data.data
						if(res.data.data.length>=20){
							this.allowHui= true
							this.loadingHui =false
						}else{
							this.allowHui= false
							this.loadingHui =false
							this.showTitle5 = true
						}
					}else{
						this.huiList =this.huiList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowHui= true
							this.loadingHui =false
						}else{
							this.allowHui= false
							this.loadingHui =false
							this.showTitle5 = true
						}
					}
					
				}
			})
		},
		// 小申展
		searchXiao(){
			this.$api.et_index_search({
				search:this.value,
				page:this.pageXiao
			}).then(res=>{
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.pageXiao===1){
						uni.hideLoading();
						this.xiaoList = res.data.data
						if(res.data.data.length>=20){
							this.allowXiao= true
							this.loadingXiao =false
						}else{
							this.allowXiao= false
							this.loadingXiao =false
							this.showTitle4 = true
						}
					}else{
						this.xiaoList =this.xiaoList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowXiao= true
							this.loadingXiao =false
						}else{
							this.allowXiao= false
							this.loadingXiao =false
							this.showTitle4 = true
						}
					}
					
				}
			})
		},
		// 路顶记
		searchLu(){
			this.$api.search_ppt({
				search:this.value,
				page:this.pageLu
			}).then(res=>{
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.pageLu===1){
						uni.hideLoading();
						this.luList = res.data.data
						if(res.data.data.length>=20){
							this.allowLu= true
							this.loadingLu =false
						}else{
							this.allowLu= false
							this.loadingLu =false
							this.showTitle3 = true
						}
					}else{
						this.luList =this.luList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowLu= true
							this.loadingLu =false
						}else{
							this.allowLu= false
							this.loadingLu =false
							this.showTitle3 = true
						}
					}
					
				}
			})
		},
		// 资讯
		searchInformation(){
			this.$api.search_news_v2({
				search:this.value,
				page:this.pageInformation
			}).then(res=>{
				// console.log(res)
				if(res.data.code=== '200'){
					if(this.pageInformation===1){
						uni.hideLoading();
						this.informationList = res.data.data
						if(res.data.data.length>=20){
							this.allowInformation= true
							this.loadingInformation =false
						}else{
							this.allowInformation= false
							this.loadingInformation =false
							this.showTitle2 = true
						}
					}else{
						this.informationList =this.informationList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowInformation= true
							this.loadingInformation =false
						}else{
							this.allowInformation= false
							this.loadingInformation =false
							this.showTitle2 = true
						}
					}
					
				}
			})
		},
		// 人脉
		searchPerson(){
			this.$api.search_user_v2({
				search:this.value,
				page:this.pagePerson
			}).then(res=>{
				if(res.data.code=== '200'){
					if(this.pagePerson===1){
						uni.hideLoading();
						this.personList = res.data.data
						if(res.data.data.length>=20){
							this.allowPerson= true
							this.loadingPerson =false
							
						}else{
							this.allowPerson= false
							this.loadingPerson =false
							this.showTitle1 = true
						}
					}else{
						this.personList =this.personList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowPerson= true
							this.loadingPerson =false
						}else{
							this.allowPerson= false
							this.loadingPerson =false
							this.showTitle1 = true
						}
					}
					
				}
			})
		},
		// 全部
		searchIndex(){
			this.$api.search({
				key:this.value,
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				// console.log(res)
				uni.hideLoading();
				if(res.data.code === '200'){
					this.globalList = res.data.data
				}
			})
		},
		everybody(){
			this.$api.Swiper({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
				
			}).then(res=>{
				// console.log(res)
				this.hot_search = res.data.data.hot_search
			})
		},
		onChange(e){
			console.log(e)
			this.value = e.detail
			if(this.value==''){
				this.n=0
			}
			uni.showLoading({
			    title: '加载中'
			});
			
			if(this.n === 0){
				setTimeout(()=>{
					this.searchIndex()
				}, 300);
			}
			
			if(this.n === 1){
				this.pagePerson = 1
				setTimeout(()=>{
					this.searchPerson()
				}, 300);
			}
			
			if(this.n === 2){
				this.pageInformation = 1
				setTimeout(()=>{
					this.searchInformation()
				}, 300);
			}
			
			if(this.n === 3){
				this.pageLu = 1
				setTimeout(()=>{
					this.searchLu()
				}, 300);
			}
			
			if(this.n === 4){
				this.pageXiao = 1
				setTimeout(()=>{
					this.searchXiao()
				}, 300);
			}
			
			if(this.n === 5){
				this.pageHui = 1
				setTimeout(()=>{
					this.searchHui()
				}, 300);
			}
			
		},
		onCancel(){
			this.value=''
			this.n = 0
		}
	}
}
